import Cookies from 'js-cookie'; //     cookie

//     
class MainApi {
  #url;
  #headers;

  constructor(data) {
    this.#url = data.url; //   
    this.#headers = {
      ...data.headers,
      authorization: `Bearer ${Cookies.get('auth_token')}`,
    };
  }

  //   
  #handleResponse(res) {
    if (res.ok) {
      return res.json()
    } else {
      return Promise.reject(`: ${res.status}`)
    }
  }

  //       (email  )
  getUserInfo() { //    get
    return fetch(`${this.#url}/users/me`, {
      headers: this.#headers,
      credentials: 'include', //      
    })
      .then(this.#handleResponse)
  }

  //     
  getMovies() {
    return fetch(`${this.#url}/movies`, {
      headers: this.#headers,
      credentials: 'include', //      
    })
      .then(this.#handleResponse)
  }

  //  
  updateUserInfo({ name, email }) {
    return fetch(`${this.#url}/users/me`, {
      method: 'PATCH',
      headers: this.#headers,
      credentials: 'include', //      
      body: JSON.stringify({
        name: name, // 
        email: email // 
      })
    })
      .then(this.#handleResponse)
  }

  //    
  createMovie(data) {
    return fetch(`${this.#url}/movies`, {
      method: 'POST',
      headers: this.#headers,
      credentials: 'include', //      
      body: JSON.stringify(data) // country, director, duration, year, description, image, trailerLink, nameRU, nameEN, thumbnail, movieId
    })
      .then(this.#handleResponse)
  }

  //    
  deleteMovie(id) {
    return fetch(`${this.#url}/movies/${id}`, {
      method: 'DELETE',
      credentials: 'include', //      
      headers: this.#headers
    })
      .then(this.#handleResponse)
  }
}

export default MainApi;

//  MainApi,     
export const mainApi = new MainApi({
  // url: 'http://localhost:3000',
  url: 'https://api.diploma.darpeex.nomoredomainsrocks.ru',
  headers: {
    'Content-Type': 'application/json'
  }
})