//   
import './Movies.css';
import { useState, useEffect, useContext } from 'react';
import { SearchForm } from './SearchForm/SearchForm';
import { MoviesContext } from '../../context/MoviesContext';
import { MoviesCardList } from './MoviesCardList/MoviesCardList';

export function Movies({ loadMovies, loadingError, handleLikeClick }) {
  const [moreMovies, setMoreMovies] = useState(false); //   ''
  const [movieFound, setMovieFound] = useState(true); //     ?
  const [initialMovies, setInitialMovies] = useState([]); //     
  const [filteredMovies, setFilteredMovies] = useState([]); //  
  const [moviesToShow, setMoviesToShow] = useState([]); // ,   
  const [onSubmit, setOnSubmit] = useState(false); //   submit 
  const [isChecked, setIsChecked] = useState(false); //    
  
  const [isInputFocused, setInputFocus] = useState(false); //   input  
  const [isEmpty, setIsEmpty] = useState(false); //   
  const [value, setValue] = useState(""); //   
  const movies = useContext(MoviesContext);

  useEffect(() => { //      (filteredMovies)   (initialMovies)
    if (filteredMovies.length > 0) {
      setMoviesToShow(filteredMovies);
    } else {
      setMoviesToShow(initialMovies);
    }
  }, [initialMovies, filteredMovies]);

  const useMediaQuery = (query) => { // query - -,    CSS -
    const [matches, setMatches] = useState(() => window.matchMedia(query).matches); //    `window.matchMedia(query).matches` - matches  true,    =  - (query)
    useEffect(() => { //     a...
      const media = window.matchMedia(query); // ...     -
      const listener = (event) => { // listener ,   -
        setMatches(event.matches); //    matches
      };

      media.addEventListener('change', listener); //   ,   listener     -
      return () => media.removeEventListener('change', listener); //     ,    
    }, [query]); // useEffect ,   query -  - 
    return matches; //  ,          -
  };

  const isLaptop = useMediaQuery('(max-width: 1024px)');
  const isTablet = useMediaQuery('(max-width: 768px)');
  const isMobile = useMediaQuery('(max-width: 550px)');

  const calculateCardsPerRow = () => { //    
    if (isMobile) {
      return 1;
    } else if (isTablet) {
      return 2;
    } else if (isLaptop) {
      return 3;
    } else {
      return 4;
    }
  };
  const [moviesPerRow, setMoviesPerRow] = useState(calculateCardsPerRow());

  const calculateRows = () => { //   
    if (isMobile) {
      return 5;
    } else {
      return 4;
    }
  }
  const [rowsToShow, setRowsToShow] = useState(calculateRows()); //  ,   

  useEffect(() => {
    setMoviesPerRow(calculateCardsPerRow());
    setRowsToShow(calculateRows());
  }, [isLaptop, isTablet, isMobile, onSubmit]); //     - onSubmit,  

  useEffect(() => {
    const totalCardsToShow = rowsToShow * moviesPerRow;
    //    (slice)      0  totalCardsToShow
    const moviesToDisplay = (isChecked ? filteredMovies : initialMovies).slice(0, totalCardsToShow);
    setMoviesToShow(moviesToDisplay);

    if (totalCardsToShow >= (isChecked ? filteredMovies.length : initialMovies.length)) {
      setMoreMovies(false);
    } else {
      setMoreMovies(true);
    }
  }, [rowsToShow, initialMovies, filteredMovies, moviesPerRow]);

  const handleMoreMoviesClick = () => { //   rowsToShow  1
    setRowsToShow((prevRows) => {
      const newRowsNumber = prevRows + 1;
      if (newRowsNumber !== null && newRowsNumber !== undefined) {
        localStorage.setItem('rowsToShow', JSON.stringify(newRowsNumber)); //      localStorage
      }
      return newRowsNumber;
    });
  };

  useEffect(() => { //      localStorage 
    const localRowsToShow = JSON.parse(localStorage.getItem('rowsToShow'));
    if (localRowsToShow !== null && localRowsToShow !== undefined) {
      setRowsToShow(localRowsToShow)
    }
  }, []);

  // 

  useEffect(() => { //      localStorage 
    const localValue = JSON.parse(localStorage.getItem('value'));
    if (localValue !== null && localValue !== undefined) {
      setValue(localValue)
    }
  }, []);

  useEffect(() => { //      localStorage
    const localMovies = JSON.parse(localStorage.getItem('movies'));
    if (localMovies !== null && localMovies !== undefined) {
      setInitialMovies(localMovies)
    }
  }, []);

  useEffect(() => { //      
    if (value !== "") { //      *
      setIsEmpty(false)
    }
  }, [value]);

  function handleValueChange(e) { //       value
    setValue(e.target.value); //      value*
  }

  const filmsProcessing = (data) => {
    setOnSubmit(!onSubmit) //  ,    
    const foundMovies = searchMovies(data); //   ,       
    setInitialMovies(foundMovies); //     
    setMovieFound(foundMovies.length > 0); //   movieFound (  )
    if (foundMovies !== null && foundMovies !== undefined) {
      localStorage.setItem('movies', JSON.stringify(foundMovies)); //     localStorage
    }
  }

  function handleSubmitForm(e) { //      
    e.preventDefault();
    if (value === "") {
      setIsEmpty(true);
    } else {
      setIsEmpty(false);
      if (value !== null && value !== undefined) {
        localStorage.setItem('value', JSON.stringify(value)); //     localStorage
      }
      if (movies.length === 0) { // ,     
        loadMovies((moviesInfo) => {
          filmsProcessing(moviesInfo)
        });
      } else {
        filmsProcessing(movies)
      }
    }
  }

  const searchMovies = (movies) => { //      
    const foundMovies = movies.filter(movie => { //    
      const title = movie.nameRU || movie.nameEN;
      return title.toLowerCase().includes(value.toLowerCase())
    });
    return foundMovies;
  }

  //  filterBox
  useEffect(() => { //      localStorage 
    const localIsChecked = JSON.parse(localStorage.getItem('isChecked'));
    if (localIsChecked !== null) {
      setIsChecked(localIsChecked)
    }
  }, []);

  const handleOnChange = (e) => { //  
    const checkboxEvent = e.target.checked // ,       ,       
    setIsChecked(checkboxEvent); //     
    if (checkboxEvent !== null && checkboxEvent !== undefined) {
      localStorage.setItem('isChecked', JSON.stringify(checkboxEvent)); //     localStorage
    }
  }

  useEffect(() => {
    if (isChecked) {
      const shortMovies = initialMovies.filter(movie => movie.duration <= 40); //  -  40 
      setFilteredMovies(shortMovies); //  
    } else {
      return setFilteredMovies(initialMovies); // ,   
    }
  }, [isChecked, initialMovies]); // ,    
  //

  const errorConditions = loadingError || (loadingError !== null) || !movieFound; // ,      
  const additionalBlockConditions = movieFound && !moreMovies //  ,    . 

  const erroronditionsMovies = movieFound && !errorConditions && (moviesToShow.length !== 0); // ,      
  const erroronditionsButtonMore = movieFound && moreMovies && !errorConditions; // ,     ""
  const erroronditionsAdittionalBlock = !movieFound || (!errorConditions && additionalBlockConditions); // ,     

  return (
    <main className="content">
      <SearchForm handleSubmitForm={handleSubmitForm} value={value} setInputFocus={setInputFocus} handleValueChange={handleValueChange} isInputFocused={isInputFocused} isEmpty={isEmpty} isChecked={isChecked} handleOnChange={handleOnChange} />
      {erroronditionsMovies && <MoviesCardList moviesToShow={moviesToShow} handleLikeClick={handleLikeClick} />}
      {erroronditionsButtonMore && <button className="content__button-more" type="button" onClick={handleMoreMoviesClick}></button>}
      {erroronditionsAdittionalBlock && <div className="content__additional-block">
        {!movieFound && <p className="content__additional-block_result_empty">  </p>}
      </div>}
      {loadingError && <div className="content__error-additional-block">
        <p className="content__error-additional-block_result_error">{loadingError}</p>
      </div>}
    </main>
  )
};
