//  
import './Register.css';
import React, { useState } from 'react';
import * as auth from '../../../utils/Auth';
import { Link, useNavigate } from 'react-router-dom';
import { ROUTES } from '../../../constants/constants';
import { Formik, Form, Field, ErrorMessage } from 'formik'; //      , Profile  -   

export const Register = ({ handleLogin, onResult, error, setError, setIsLoading }) => {
  const [isFormValid, setIsFormValid] = useState(false);
  const navigate = useNavigate();

  return (
    <main>
      <section className="register">
        <h1 className="register__welcome"> !</h1>
        <Formik
          initialValues={{ name: '', email: '', password: '' }}
          validateOnChange={true} //      ()
          validate={(values) => {
            const errors = {};
            if (!values.name) {
              errors.name = ',  ';
            } else if (!/^[a-zA-Z--\s-]+$/.test(values.name)) {
              errors.name = " : , ,   ";
            } else if (!(2 <= values.name.length)) {
              errors.name = '     2 ';
            } else if ((values.name.length >= 30)) {
              errors.name = '     30 ';
            }
            if (!values.email) {
              errors.email = ',  e-mail';
            } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
              errors.email = ' e-mail ';
            }
            if (!values.password) {
              errors.password = ',  ';
            } else if (!(8 <= values.password.length)) {
              errors.password = '    8 ';
            }
            setIsFormValid(Object.keys(errors).length === 0);
            setError('')
            return errors;
          }}
          validateOnMount //     
          onSubmit={(values, { setSubmitting }) => {
            setIsLoading(true)
            auth.register(values.name, values.password, values.email)
              .then((res) => {
                onResult(true)
                auth.login(values.password, values.email) //       /movies
                  .then((data) => {
                    handleLogin();
                    navigate(ROUTES.MOVIES, { replace: true });
                  }).catch(err => {
                    console.log(err)
                  }).finally(() => setIsLoading(false));
              })
              .catch((err) => {
                onResult(false)
                setError('Email    ')
              })
              .finally(() => {
                setIsLoading(false)
                setSubmitting(false)
              })
          }}
        >
          {({ isSubmitting, errors, touched }) => (
            <Form className="register__form" noValidate> {/* noValidate -    */}
              <label htmlFor="name" className="register__label"></label>
              <Field type="text" name="name" className={`register__form-input ${errors.name && touched.name ? "register__form-input_error" : ""}`} placeholder=" " />

              <ErrorMessage name="name" component="div" className="register__form-error" />

              <label htmlFor="email" className="register__label">E-mail</label>
              <Field type="email" name="email" className={`register__form-input ${errors.email && touched.email ? "register__form-input_error" : ""}`} placeholder="example@email.ru" />

              <ErrorMessage name="email" component="div" className="register__form-error" />

              <label htmlFor="password" className="register__label"></label>
              <Field type="password" name="password" className={`register__form-input ${errors.password && touched.password ? "register__form-input_error" : ""}`} placeholder=" " />

              <ErrorMessage name="password" component="div" className="register__form-error" />

              <div className="register__button-container">
                {(error !== '') && <span className="register__error-message">{error}</span>}
                <button type="submit" className="register__button" disabled={isSubmitting || !isFormValid}>
                  
                </button>
              </div>

              <div className="register__signup">
                <p className="register__text-signup"> ?</p>
                <Link to={ROUTES.SIGNIN} className="register__signin-button"></Link>
              </div>
            </Form>
          )}
        </Formik>
      </section>
    </main>
  )
}