// 
import './Profile.css';
import { Link } from 'react-router-dom';
import { ROUTES } from '../../../constants/constants';
import { useEffect, useState, useContext } from "react";
import { CurrentUserContext } from "../../../context/CurrentUserContext";

export const Profile = ({ onUpdateUser, handleDeleteTocken, error, result }) => { //    error,     disabled={!isValid} 
  const [isEditProfileActive, setIsEditProfileActive] = useState(false);
  const [showMessage, setShowMessage] = useState(false);
  const [isValid, setIsValid] = useState(false);
  const [nameError, setNameError] = useState(false);
  const [emailError, setEmailValid] = useState(false);
  const [isNameValid, setIsNameValid] = useState(false);
  const [isEmailValid, setIsEmailValid] = useState(false);

  const currentUser = useContext(CurrentUserContext); //    
  const [name, setName] = useState(''); //  
  const [email, setEmail] = useState(''); //  
  const [initialName, setInitialName] = useState(''); //    
  const [initialEmail, setInitialEmail] = useState(''); // Email   
  const [isFieldDifferent, setIsFieldDifferent] = useState(false); //      

  //      API       .
  useEffect(() => {
    setName(currentUser.name ?? ''); //        -  
    setEmail(currentUser.email ?? ''); //        -  
  }, [currentUser]);  //     ,       -     

  useEffect(() => { //     currentUser
    if (currentUser.name !== '' && currentUser.email !== '') {
      setInitialName(currentUser.name)
      setInitialEmail(currentUser.email)
    }
  }, [currentUser, initialName, initialEmail]);

  useEffect(() => { // ,       -  
    if (name === initialName && email === initialEmail) { //        
      setIsFieldDifferent(false)
    } else { //      - 
      setIsFieldDifferent(true)
    }
  }, [name, email, initialName, initialEmail, isFieldDifferent, isEditProfileActive]); //   (isValid & setIsFieldDifferent),       

  useEffect(() => {
    //  
    if (!name) {
      setIsNameValid(false)
      setNameError(',  ');
    } else if (!/^[a-zA-Z--\s-]+$/.test(name)) {
      setIsNameValid(false)
      setNameError(" : , ,   ");
    } else if (!(2 <= name.length)) {
      setIsNameValid(false)
      setNameError('     2 ');
    } else if ((name.length >= 30)) {
      setIsNameValid(false)
      setNameError('     30 ');
    } else {
      setNameError('');
      setIsNameValid(true)
    }

    //  
    if (!email) {
      setIsEmailValid(false)
      setEmailValid(',  e-mail');
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) {
      setIsEmailValid(false)
      setEmailValid(' e-mail ');
    } else {
      setEmailValid('');
      setIsEmailValid(true)
    }

    //   
    if (!isEmailValid || !isNameValid) {
      setIsValid(false)
    } else if (isEmailValid && isNameValid) {
      setIsValid(true)
    }
  }, [name, email, nameError, emailError, isEditProfileActive]); // error ,          
  //    ,         -     

  const handleEditProfile = () => { //  
    setIsEditProfileActive(!isEditProfileActive);
  }

  function handleNameChange(e) { //      name    
    setName(e.target.value);
  }
  function handleEmailChange(e) { //      email    
    setEmail(e.target.value);
  }

  const handleSubmit = (evt) => { //  
    evt.preventDefault(); //      
    onUpdateUser({ name, email }); //       
    setIsFieldDifferent(false); //   false
  }

  useEffect(() => {
    if (result === true) {
      setShowMessage(true);
      const timer = setTimeout(() => { //  ,     3 
        setShowMessage(false);
      }, 3000);

      return () => { //     
        clearTimeout(timer);
      };
    }
  }, [result]);

  const handleSignout = () => { // ,     localStorage
    handleDeleteTocken();
  }

  return (
    <main>
      <section className="profile">
        <h1 className="profile__title">, {name}!</h1> {/*     */}
        <form className="profile__form" noValidate onSubmit={handleSubmit}>
          <div className="profile__form-container">
            <p className="profile__form-text profile__field"></p>
            <input
              name="name"
              value={name} //    
              onChange={handleNameChange} //    ,      
              className="profile__form-text profile__form-input profile__form-input_field_name"
              type="text"
              minLength="2"
              maxLength="40"
              disabled={!isEditProfileActive && 'disabled'}
              placeholder=""
              required />
          </div>
          <hr className="profile__underline" />
          <div className="profile__form-container">
            <p className="profile__form-text profile__field">E&#8209;mail</p>
            <input
              name="email"
              value={email} //    
              onChange={handleEmailChange} //   ,      
              className="profile__form-text profile__form-input profile__form-input_field_email"
              type="text"
              placeholder="pochta@yandex.ru"
              minLength="2"
              maxLength="200"
              disabled={!isEditProfileActive && 'disabled'}
              required />
          </div>
          <div className={`profile__container ${isEditProfileActive ? "profile__saved-btn" : ""}`}>
            {!isEditProfileActive && <button type="button" className="profile__button profile__edit-button" onClick={handleEditProfile}></button>}
            {!isEditProfileActive && <Link to={ROUTES.HOME} className="profile__button profile__signout-button" onClick={handleSignout}>  </Link>}

            {isEditProfileActive &&(error === '') && showMessage &&  <span className="profile__result-text">  </span>}
            {isEditProfileActive && (error !== '') && <span className="profile__error-text">{error}</span>}
            {isEditProfileActive && !isValid && (nameError !== '') && <span className="profile__error-text">{nameError}</span>}
            {isEditProfileActive && !isValid && (emailError !== '') && <span className="profile__error-text">{emailError}</span>}
            {isEditProfileActive && <button type="submit" disabled={!isValid || !isFieldDifferent} className={`profile__button profile__saved-button ${!isValid || !isFieldDifferent ? "profile__saved-button_inactive" : ""}`} >
              
            </button>}
            {isEditProfileActive && <button className="profile__navigate-button" type="button" onClick={handleEditProfile}> {/*   '() => navigate(-1)' */}
              
            </button>}
          </div>
        </form>
      </section>
    </main>
  )
}