//    
import './SavedMovies.css';
import { useState, useContext, useEffect } from 'react';
import { SearchForm } from '../Movies/SearchForm/SearchForm';
import { SavedMoviesContext } from '../../context/SavedMoviesContext';
import { SavedMoviesCardList } from '../SavedMovies/SavedMoviesCardList/SavedMoviesCardList';

export function SavedMovies({ loadingError, handleDeleteClick }) {
  const savedMovies = useContext(SavedMoviesContext); //   +
  const [isStorageMoviesInstalled, setIsStorageMoviesInstalled] = useState(false); //      ? +
  const [filteredMovies, setFilteredMovies] = useState([]); //   +
  const [isInputFocused, setInputFocus] = useState(false); //   input   +
  const [initialMovies, setInitialMovies] = useState([]); //      +
  const [moviesToShow, setMoviesToShow] = useState([]); // ,    +
  const [movieFound, setMovieFound] = useState(true); //     ? +
  const [isChecked, setIsChecked] = useState(false); //     +
  const [onSubmit, setOnSubmit] = useState(false); //   submit  +
  const [isEmpty, setIsEmpty] = useState(false); //    +
  const [value, setValue] = useState(""); //    +

  useEffect(() => { //      (filteredMovies)   (initialMovies)
    if (isChecked) {
      setMoviesToShow(filteredMovies);
    } else {
      setMoviesToShow(initialMovies);
    }
  }, [initialMovies, filteredMovies]);

  const filmsProcessing = () => {
    setOnSubmit(!onSubmit) //  ,    
    const foundMovies = searchMovies(savedMovies);
    setInitialMovies(foundMovies); //     
    setMovieFound(foundMovies.length > 0); //   movieFound (  )
    if (foundMovies !== null && foundMovies !== undefined) {
      localStorage.setItem('savedMovies', JSON.stringify(foundMovies)); //     localStorage
    }
  }

  useEffect(() => { //    ,      localStorage
    if (isStorageMoviesInstalled === true) { //   ,      
      filmsProcessing();
    }
  }, [savedMovies]);

  function handleSubmitForm(e) { //      
    e.preventDefault();
    if (value === "") {
      setIsEmpty(true);
    } else {
      setIsEmpty(false);
      if (value !== null && value !== undefined) {
        localStorage.setItem('savedMoviesValue', JSON.stringify(value)); //     localStorage
      }
      filmsProcessing(savedMovies)
    }
  }

  const searchMovies = (savedMovies) => { //      
    const foundMovies = savedMovies.filter(movie => { //    
      const title = movie.nameRU || movie.nameEN;
      return title.toLowerCase().includes(value.toLowerCase())
    });
    return foundMovies;
  }

  function handleValueChange(e) { //       value
    setValue(e.target.value); //      value*
  }

  useEffect(() => { //      localStorage 
    const localValue = JSON.parse(localStorage.getItem('savedMoviesValue'));
    if (localValue !== null && localValue !== undefined) {
      setValue(localValue)
    }
  }, []);

  useEffect(() => { //      localStorage
    const localMovies = JSON.parse(localStorage.getItem('savedMovies'));
    if (localMovies !== null && localMovies !== undefined) {
      setInitialMovies(localMovies)
    }
  }, []);

  useEffect(() => { //      
    if (value !== "") { //      *
      setIsEmpty(false)
    }
  }, [value]);

  useEffect(() => { //      localStorage 
    const localIsChecked = JSON.parse(localStorage.getItem('savedMoviesIsChecked'));
    if (localIsChecked !== null) {
      setIsChecked(localIsChecked);
      setIsStorageMoviesInstalled(true);
    }
  }, []);

  const handleOnChange = (e) => { //  
    const checkboxEvent = e.target.checked // ,       ,       
    setIsChecked(checkboxEvent); //     
    if (checkboxEvent !== null && checkboxEvent !== undefined) {
      localStorage.setItem('savedMoviesIsChecked', JSON.stringify(checkboxEvent)); //     localStorage
    }
  }

  useEffect(() => {
    if (isChecked) {
      const shortMovies = initialMovies.filter(movie => movie.duration <= 40); //  -  40 
      setFilteredMovies(shortMovies); //  
    } else {
      return setFilteredMovies(initialMovies); // ,   
    }
  }, [isChecked, initialMovies]);

  const errorConditions = loadingError || (loadingError !== null) || !movieFound; // ,      
  const erroronditionsMovies = movieFound && !errorConditions && (savedMovies.length !== 0); // ,      
  const erroronditionsAdittionalBlock = !movieFound || !errorConditions; // ,     

  return (
    <main className="content">
      <SearchForm handleSubmitForm={handleSubmitForm} value={value} setInputFocus={setInputFocus} handleValueChange={handleValueChange} isInputFocused={isInputFocused} isEmpty={isEmpty} isChecked={isChecked} handleOnChange={handleOnChange} />
      {erroronditionsMovies && <SavedMoviesCardList moviesToShow={moviesToShow} handleDeleteClick={handleDeleteClick} />}
      {erroronditionsAdittionalBlock && <div className="content__additional-block">
        {!movieFound && <p className="content__additional-block_result_empty">  </p>}
      </div>}
      {loadingError && <div className="content__error-additional-block">
        <p className="content__error-additional-block_result_error">{loadingError}</p>
      </div>}
    </main>
  )
};
