//    ''  '  '
export const convertDurationToHourlyFormat = (movie) => {
  const duration = movie.duration; //  
  const remainder = (duration % 60) //     60
  const durationToHour = (duration / 60); //     60
  if (duration >= 60) { //     
    return `${Math.floor(durationToHour)} ${remainder}`
  } else { //    -  
    return `${remainder}`;
  }
}