//   
import './ErrorPage.css';
import { useNavigate } from 'react-router-dom';

export function ErrorPage({ error }) {
	const navigate = useNavigate();
	// const errorType = error.statusCode;
	const errorType = 404;
	function errorTypeText() {
		if (errorType === 404) {
			return "     .";
		} else if (errorType === 500) {
			return "   .";
		}
	}

	return ( //  error-page
		<main>
			<section className="error-page">
				<div className="error-page__container">
					<h1 className="error-page__title">{errorType}</h1> {/*    404  500 - {errorType}*/}
					<p className="error-page__description">{errorTypeText(errorType)}</p>  {/* , ,  - {errorTypeText}*/}
					<button className="error-page__navigate" type="button" onClick={() => navigate(-1)}></button>
				</div>
			</section>
		</main>
	)
}
