import '../../index.css'; // 
import Cookies from 'js-cookie'; //  Cookies
import { ROUTES } from '../../constants/constants';
import React, { useState, useEffect } from 'react'; //  
import { Route, Routes, Navigate, useLocation } from 'react-router-dom'; // Routes  

import { ProtectedRouteElement } from "../shared/ProtectedRoute"; //  HOC
import { moviesApi } from '../../utils/MoviesApi'; //   
import { mainApi } from '../../utils/MainApi'; //   

import { Main } from '../Main/Main';
import * as auth from '../../utils/Auth';
import { Movies } from '../Movies/Movies';
import { Header } from '../shared/Header/Header';
import { Footer } from '../shared/Footer/Footer';
import { Login } from '../authentication/Login/Login';
import { SavedMovies } from '../SavedMovies/SavedMovies';
import { Preloader } from '../shared/Preloader/Preloader';
import { ErrorPage } from '../shared/ErrorPage/ErrorPage';
import { MoviesContext } from '../../context/MoviesContext';
import { Profile } from '../authentication/Profile/Profile';
import { Register } from '../authentication/Register/Register';
import { SavedMoviesContext } from '../../context/SavedMoviesContext';
import { CurrentUserContext } from '../../context/CurrentUserContext';

function App() {
  const [loadingError, setloadingError] = useState(null);
  const [appIsReady, setAppIsReady] = useState(false);
  const [currentUser, setCurrentUser] = useState({});
  const [savedMovies, setSavedMovies] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [loggedIn, setLoggedIn] = useState(false);
  const [userData, setUserData] = useState('');
  const [result, setResult] = useState(false);
  const [movies, setMovies] = useState([]);
  const [error, setError] = useState('');

  const location = useLocation(); //   location,   URL

  //     
  useEffect(() => {
    if (loggedIn) {
      setIsLoading(true); //  
      mainApi.getMovies() //    
        .then((data) => {
          setSavedMovies(data); //   
        })
        .catch((err) => {
          console.log(`: ${err}`)
        })
        .finally(() => setIsLoading(false)); //  ;
    }
  }, [loggedIn]);

  const tokenCheck = () => { //       cookie,     
    auth.checkToken().then((res) => { //  
      if (res) {
        const userData = { //     !
          email: res.email
        }
        setLoggedIn(true); //  
        setUserData(userData)
      }
    }).catch((err) => {
      setAppIsReady(true) //      ProtectedRoute   .
      console.log(`: ${err}`)
    })
      .finally(() => setIsLoading(false)); //  ;
  }

  //     
  useEffect(() => {
    if (loggedIn !== true) { // ,    ,     
      tokenCheck();
    }
  }, [loggedIn])

  //     
  useEffect(() => {
    if (loggedIn) {
      setIsLoading(true); //  
      mainApi.getUserInfo() //     
        .then((userInfo) => { //    
          setAppIsReady(true) //      ProtectedRoute   .
          setCurrentUser(userInfo); //       
        })
        .catch((err) => {
          console.log(`: ${err}`)
        })
        .finally(() => setIsLoading(false)); //  ;
    }
  }, [loggedIn]);

  //     
  function handleUpdateUser({ name, email }) { //        (submit)
    setError('')
    setResult(false)
    setIsLoading(true); //  
    mainApi.updateUserInfo({ name, email }).then((userInfo) => { //   userInfo,        { name, description }...
      setCurrentUser(userInfo); // ...   ,       currentUser
      setResult(true)
    })
      .catch((err) => {
        setResult(false)
        console.log(`: ${err}`)
        setError('    .')
      })
      .finally(() => {
        setIsLoading(false)
      }); //  ;
  }

  //    
  async function loadMovies(onSuccess) {
    if (loggedIn) { //  
      setIsLoading(true); //  
      try {
        const moviesInfo = await moviesApi.getInitialMovies() //    
        setMovies(moviesInfo); //    p.s.   ,    ,    ) 
        onSuccess(moviesInfo); // ..    ,    ,     ,    movies
        setloadingError(null); //    ,  
        setIsLoading(false); //  
      } catch (err) {
        setIsLoading(false); //  
        console.log(`: ${err}`)
        setloadingError("    . ,      .      ");
      }
    }
  }

  //   
  const handleLogin = () => {
    setLoggedIn(true);
  }

  //       Movies
  function handleLikeClick(movie) {
    const isMovieSaved = savedMovies.some(film => film.movieId === movie.id);
    const apiUrl = "https://api.nomoreparties.co";

    if (isMovieSaved) { //    -> 
      setIsLoading(true); //  
      const movieBeingDeleted = savedMovies.find((film) => film.movieId === movie.id)
      mainApi.deleteMovie(movieBeingDeleted._id).then(() => {
        setSavedMovies((state) => state.filter((film) => film.movieId !== movie.id)); //  savedMovies,      
      })
        .catch((err) => {
          console.log(`: ${err}`)
        })
        .finally(() => setIsLoading(false)); //  ;
    } else if (!isMovieSaved) { //    -> 
      setIsLoading(true); //  
      mainApi.createMovie({
        country: movie.country,
        director: movie.director,
        duration: movie.duration,
        year: movie.year,
        description: movie.description,
        image: `${apiUrl}${movie.image.url}`,
        trailerLink: movie.trailerLink,
        nameRU: movie.nameRU,
        nameEN: movie.nameEN,
        thumbnail: `${apiUrl}${movie.image.formats.thumbnail.url}`,
        movieId: movie.id,
      }).then((newMovie) => {
        setSavedMovies([newMovie, ...savedMovies]);
      })
        .catch((err) => {
          console.log(`: ${err}`)
        })
        .finally(() => setIsLoading(false)); //  ;
    }
  }

  //    SavedMovies
  function handleDeleteClick(movie) {
    const isMovieSaved = savedMovies.some(film => film._id === movie._id);

    if (isMovieSaved) { //    -> 
      setIsLoading(true); //  
      const movieBeingDeleted = savedMovies.find((film) => film._id === movie._id)
      mainApi.deleteMovie(movieBeingDeleted._id).then(() => {
        setSavedMovies((state) => state.filter((film) => film._id !== movie._id)); //  savedMovies,      
      })
        .catch((err) => {
          console.log(`: ${err}`)
        })
        .finally(() => setIsLoading(false)); //  ;
    }
  }

  //       
  function handleDeleteTocken() {
    Cookies.remove('jwt');
    localStorage.clear();
    setLoggedIn(false);
    setUserData({});
  }

  //     
  const handleResult = setResult;

  const { HOME, MOVIES, SAVED_MOVIES, PROFILE, SIGNUP, SIGNIN, ERROR, UNKNOWN } = ROUTES;
  const footerComponentsPaths = [HOME, MOVIES, SAVED_MOVIES];
  const footerClass = footerComponentsPaths.includes(location.pathname) //   Footer
    ? <Footer />
    : <></>;
  const headerComponentsPaths = [ERROR];
  const headerClass = headerComponentsPaths.includes(location.pathname) //   Header
    ? <></>
    : <Header location={location} userData={userData} loggedIn={loggedIn} />;

  //  
  return (
    !appIsReady ? (
      <Preloader /> //  ,      
    ) : (
      <div className="App">
        <div className="page">
          {/*      */}
          <CurrentUserContext.Provider value={currentUser}> {/*       */}
            <SavedMoviesContext.Provider value={savedMovies} > {/*  2-  -     */}
              <MoviesContext.Provider value={movies} >
                {/*   */}
                {headerClass}

                {/*  */}
                {isLoading && <Preloader />}

                {/*    */}
                <Routes>
                  <Route path={UNKNOWN} element={<Navigate to={ERROR} replace />} /> {/*   */}
                  <Route path={ERROR} element={<ErrorPage />} /> {/*    */}
                  <Route path={HOME} element={<Main />} /> {/*  */}
                  <Route path={MOVIES} element={<ProtectedRouteElement
                    element={Movies} loadMovies={loadMovies} loadingError={loadingError} handleLikeClick={handleLikeClick} loggedIn={loggedIn} />}
                  /> {/*  */} {/*  loggedIn    ,  ProtectedRouteElement     */}
                  <Route path={SAVED_MOVIES} element={<ProtectedRouteElement
                    element={SavedMovies} loadingError={loadingError} handleDeleteClick={handleDeleteClick} loggedIn={loggedIn} />}
                  /> {/*   */}
                  <Route path={PROFILE} element={<ProtectedRouteElement
                    element={Profile} onUpdateUser={handleUpdateUser} handleDeleteTocken={handleDeleteTocken} error={error} result={result} loggedIn={loggedIn} />}
                  /> {/*  */}
                  <Route path={SIGNIN} element={<Login handleLogin={handleLogin} onResult={handleResult} error={error} setError={setError} setIsLoading={setIsLoading} />} /> {/*  */}
                  <Route path={SIGNUP} element={<Register handleLogin={handleLogin} onResult={handleResult} error={error} setError={setError} setIsLoading={setIsLoading} />} /> {/*  */}
                </Routes>

                {/*   */}
                {footerClass} {/*     Profile, Login, Registr -   */}

              </MoviesContext.Provider>
            </SavedMoviesContext.Provider>
          </CurrentUserContext.Provider>
        </div>
      </div >
    )
  );
}

export default App;