//    
import './Promo.css';
import promoImage from '../../../images/promoImage.svg'

export function Promo({ scrollToRef }) {
	return ( //  Promo
		<section className="promo promo_position section">
			<img className="promo__image" src={promoImage} alt=" "></img>
			<div className="promo__description">
				<h1 className="promo__description-title">    &#8209;.</h1>
				<p className="promo__description-paragraph"> ,       &nbsp; .</p>
				<button type="button" className="promo__navigate" onClick={() => scrollToRef.current.scrollIntoView({ behavior: 'smooth' })}> </button>
			</div>
		</section>
	)
}
